import { applyMiddleware, compose, createStore } from 'redux';
import { createBrowserHistory } from 'history';
import { routerMiddleware } from 'connected-react-router';
import createSagaMiddlware from 'redux-saga';
import createRootReducer from '@redux/reducers';
import rootSaga from './sagas';

export const history = createBrowserHistory();
const sagaMiddleware = createSagaMiddlware();
const middlewres = [sagaMiddleware, routerMiddleware(history)]; //    ->    ->   

const initialState = {};
const devtools = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__; //       
const composeEnhancer = process.env.NODE_ENV === 'production' ? compose : devtools || compose; //    devtools 

const store = createStore(createRootReducer(history), initialState, composeEnhancer(applyMiddleware(...middlewres)));
sagaMiddleware.run(rootSaga);

export default store;
